<template lang="pug">
  .page
    header
      .title
        img(@click="travelToHome", src="/hopeIT_logo.png")
        bread-crumbs()
      ui-button(@click="logoutHandler") Wyloguj
    main
      router-view()
</template>

<script>
import { mapActions } from 'vuex'

import UiButton from '../components/UiButton'
import BreadCrumbs from '../components/Breadcrumbs'

export default {
  components: {
    UiButton,
    BreadCrumbs
  },
  methods: {
    logoutHandler () {
      this.logout()
      this.$router.push('/login')
    },
    ...mapActions([
      'logout'
    ]),
    travelToHome () {
      this.$router.push('/')
    }
  }
}
</script>

<style lang="scss" scoped>
  .page {
    background-color: rgb(14, 68, 142);
    background-image: url('/bg1.png');
    background-size: cover;
    background-position-y: 300px;
    background-repeat: no-repeat;
    min-height: 100vh;

    header {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      color: white;
      padding: 10px 16px;

      .title {
        display: flex;
        flex-direction: row;
        align-items: center;
        font-size: 20pt;
        font-weight: 200;
        flex-grow: 1;

        img {
          width: 64px;
          margin-right: 16px;
          cursor: pointer;
        }
      }
    }
  }
</style>




