<template lang="pug">
  .challengeList
    aside
      .stats
        stat(:value="'12 000'", :desc="'Pieniądze zgromadzone w tym miesiącu'", :money="true")
        stat(:value="'1 230'", :desc="'Ilość aktywnych darczyńców'", :money="false")
      .chart
        dotation-chart(:dataSet="dataSet", :labels="labels", :mainlabel="'Zgromadzone razem'")
      h1.topMargin Wiadomości
        ui-button.messagesButton(@click="$router.push('/messages')")
          i.material-icons.sms sms
    .list
      h1 Lista Wyzwań
      ui-list(
        :elements="challenges",
        :actions="actions",
        @add="$router.push('/challenges/new')",
        @click="selectChallenge($event)"
      )
        .name(slot-scope="props")
          .img(:style="{ backgroundImage: 'url('+getImageUrl(props.scope.image_url)+')' }")
          .title() {{props.scope.title}}
</template>

<script>
import { API_BASE } from '../api/config.js'
import { mapState, mapActions } from 'vuex'
import DotationChart from '../components/DotationChart'

import UiList from '../components/UiList'
import UiButton from '../components/UiButton'
import Stat from '../components/Stat'

export default {
  name: 'ChallengeList',
  components: {
    UiList,
    UiButton,
    Stat,
    DotationChart
  },
  data () {
    return {
      chart: null,
      one: true,
      dataSet: [100, 200, 500, 250, 300, 700],
      labels: ['Czerwiec', 'Lipiec', 'Sierpień', 'Wrzesień', 'Październik', 'Listopad']
    }
  },
  mounted () {
    this.loadChallenges()
  },
  computed: {
    ...mapState([
      'challenges'
    ]),
    actions () {
      return [{ name: 'add', label: 'Dodaj', icon: 'add' }]
    }
  },
  methods: {
    toggleData () {
      this.one = !this.one
      this.chart.data.datasets[0].data = (this.one) ? this.data1 : this.data2
      this.chart.update()
    },
    ...mapActions([
      'loadChallenges'
    ]),
    selectChallenge (c) {
      this.$router.push(`/challenges/${c._id}`)
    },
    getImageUrl (url) {
      return API_BASE + url
    }
  }
}
</script>

<style lang="scss" scoped>
.challengeList {
  margin-top: 32px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-bottom: 20px;

  .name {
    margin: 0 16px;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .img {
    margin-right: 16px;
    width: 48px;
    height: 48px;
    overflow: hidden;
    border-radius: 32px;
    background-size: cover;
  }

  aside {
    display: flex;
    flex-direction: column;
    min-height: 400px;
  }

  .stats {
    margin-top: 100px;
    display: flex;
    flex-direction: row;
  }

  .chart {
    flex-grow: 1;
    min-width: 800px;
    margin-right: 80px;
    margin-top: 20px;
  }

  #mainChart {
    margin-top: 20px;
    margin-right: 80px;
  }

  h1 {
    font-size: 42pt;
    font-weight: 200;
    margin: 0 0 16px 0;
    color: white;
  }

  .topMargin {
    margin-top: 20px;
    display: inline-block;
  }

  .sms {
    font-size: 32pt;
    color: white;
    margin: 8px 5px 5px 5px;
    width: 64px;
  }

  .messagesButton {
    margin-left: 32px;
    cursor: pointer;
  }
}
  
</style>





