<template lang="pug">
  .newChallenge
    .form
      .columns
        .first-column.column
          h2 Nazwa wyzwania
          ui-input.name(:value="name", @input="name = $event", placeholder="Nazwa wyzwania")
          h2 Opis
          ui-input.description(v-model="description", placeholder="Opis")
          h2 Obraz
          vue-picture-input(
            ref="pictureInput" 
            @change="onImageChange"
            width="300"
            height="300"
          )
        .second-column.column
          h2 Początkowi wyzwani
          ui-list(:elements="people", :actions="actions", :newSlot="peopleAdding", @add="peopleAdding = true", @commit="commitPerson")
            .person(slot-scope="props") {{props.scope.name}}
            .new(slot="newSlot")
              vue-multiselect(
                 :options="options"
                 @search-change="search"
                 :internal-search="false"
                 label="name"
                 track-by="_id",
                 v-model="select"
              )
      .centerWrapper
        ui-button(@click="handleAdd") 
          i.material-icons save
          | &nbsp;Zapisz
</template>

<script>
// import _ from 'lodash'
import api from '../api/api.js'
import VueMultiselect from 'vue-multiselect'
import VuePictureInput from 'vue-picture-input'
import UiButton from '../components/UiButton'
import UiList from '../components/UiList'
import UiInput from '../components/UiTextInput'

export default {
  name: 'NewChallenge',
  components: {
    VuePictureInput,
    UiButton,
    UiList,
    UiInput,
    VueMultiselect
  },
  data () {
    return {
      name: '',
      description: '',
      image: '',
      people: [], // users [_id, _id, _id]
      normalActions: [{ name: 'add', label: 'Nowy', icon: 'person_add' }],
      addingActions: [{ name: 'commit', label: 'Dodaj', icon: 'add_box' }],
      peopleAdding: true,
      options: [],
      select: undefined
    }
  },
  computed: {
    actions () {
      return this.peopleAdding ? this.addingActions : this.normalActions
    }
  },
  methods: {
    onImageChange () {
      const base64 = this.$refs.pictureInput.image
      this.image = base64
    },
    handleAdd () {
      api.addChallenge({
        name: this.name,
        description: this.description,
        image: this.image,
        users: this.people.map(person => person._id)
      }).then(() => this.$router.push('/challenges'))
    },
    search (query) {
      api.searchUsers(query).then((res) => { this.options = res.data })
    },
    commitPerson () {
      this.people = [this.select, ...this.people]
      this.peopleAdding = false
      this.options = []
      this.select = undefined
    }
  }
}
</script>

<style lang="scss">
  .newChallenge {
    .form {
      h2 {
        font-size: 16px;
        font-weight: 200;
        color: white;
      }
      .columns {
        display: flex;
        flex-direction: row;
        justify-content: center;

        .column {
          flex-grow: 1;
          max-width: 600px;
          margin: 0 16px;
        }

        .first-column {
          display: flex;
          flex-direction: column;


          .name, .description {
            height: 64px;
          }
        }

        .second-column {
          .new {
            width: 100%;
            display: flex;
            flex-direction: columns;
            justify-content: center;

            .multiselect__tags {
              border: none;
            }
          }

          .person {
            margin: 0 16px;
          }
        }
      }
    }
  }

  .centerWrapper {
    margin-top: 32px;
    text-align: center;
  }
</style>




