import axios from 'axios'

import store from '../store'

import { API_BASE } from './config.js'

const admin = axios.create({
  baseURL: API_BASE + '/admin'
})

const challenges = axios.create({
  baseURL: API_BASE + '/admin/challenges'
})

export default {
  login (username, password) {
    return admin.post('/login', {
      user: username,
      pass: password
    })
  },

  getAllChallenges () {
    const token = store.getters['authKey']
    return challenges.get('', {
      headers: { 'Authorization': `Bearer ${token}` }
    })
  },
  getChallenge (id) {
    const token = store.getters['authKey']
    return challenges.get(`/${id}`, {
      headers: { 'Authorization': `Bearer ${token}` }
    })
  },
  addChallenge (challengeData) {
    const token = store.getters['authKey']
    return challenges.post('', challengeData, {
      headers: { 'Authorization': `Bearer ${token}` }
    })
  },
  getAllUsers () {
    const token = store.getters['authKey']
    return admin.get('/user', {
      headers: { 'Authorization': `Bearer ${token}` }
    })
  },
  searchUsers (query) {
    const token = store.getters['authKey']
    return admin.post('/find', { name: query }, {
      headers: { 'Authorization': `Bearer ${token}` }
    })
  },
  getAllMessages () {
    const token = store.getters['authKey']
    return admin.get('/messages', {
      headers: { 'Authorization': `Bearer ${token}` }
    })
  },
  sendMessage (msg) {
    const token = store.getters['authKey']
    return admin.post('/messages', msg, {
      headers: { 'Authorization': `Bearer ${token}` }
    })
  }
}



