<template lang="pug">
  button(@click="$emit('click')")
    slot
</template>

<script>
export default {
  name: 'UiButton'
}

</script>

<style lang="scss" scoped>
  button {
    background-color: rgba(0,0,0, 0.5);
    color: white;
    font-size: 20pt;
    margin: 5px;
    border: none;
    border-radius: 5px;
    padding: 2px 8px;
    font-family: 'Open Sans', sans-serif;
    text-align: center;
  }
</style>



