<template lang="pug">
  .messageView
    .list
      ui-list(
        :elements="computedElements",
        :actions="actions",
        :hoverAction="view === 'users' ? actions[0] : actions[1]",
        @setUsers="view = 'users'"
        @setChallenges="view = 'challenges'",
        @click="handleClick"
        :invert="true"
      )
        .element(slot-scope="props")
          .img(:style="{'background-image': 'url('+(view === 'users' ? props.scope.img_url : getImageUrl(props.scope.image_url))}")
          span.content {{view === 'users' ? props.scope.name : props.scope.title}}
    .view
      router-view()
</template>

<script>
import { API_BASE } from '../api/config.js'
import { mapGetters, mapActions, mapState } from 'vuex'
import UiList from '../components/UiList'

export default {
  mounted () {
    this.loadMessages()
    this.loadUsers()
    this.loadChallenges()
  },
  components: {
    UiList
  },
  data () {
    return {
      view: 'challenges',
      actions: [
        { name: 'setUsers', label: 'Użytkownicy', icon: 'people_outline' },
        { name: 'setChallenges', label: 'Wyzwania', icon: 'assistant' }
      ]
    }
  },
  computed: {
    ...mapGetters(['messageUsers', 'messageChallenges']),
    ...mapState(['users', 'challenges']),
    computedElements () {
      return this.view === 'users' ? this.users : this.challenges
    }
  },
  methods: {
    ...mapActions(['loadMessages', 'loadUsers', 'loadChallenges']),
    handleClick (el) {
      this.$router.push(`/messages/${this.view}/${el._id}`)
    },
    getImageUrl (url) {
      return API_BASE + url
    }
  },
  name: 'MessageView'
}
</script>


<style lang="scss" scoped>
  .messageView {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }

  .list {
    width: 570px;
    margin: 48px 16px;
    margin-left: 48px;
    margin-right: 0;
  }

  .element {
    padding: 0 16px;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .view {
    flex-grow: 2;
  }

  .img {
    width: 48px;
    height: 48px;
    background-size: cover;
    margin-right: 8px;
    border-radius: 32px;
  }
</style>



