<template lang="pug">
  .textInput
    input(:value="value", @input="$emit('input', $event.target.value)", :placeholder="placeholder")
</template>

<script>
export default {
  name: 'TextInput',
  props: ['value', 'placeholder']
}
</script>

<style lang="scss" scoped>
.textInput {
  position: relative;
  margin-bottom: 10px;
  margin-top: 0;
}
input {
      font-size: 20pt;
      border: none;
      width: 100%;
      height: 100%;
      box-sizing: border-box;
      border-radius: 5px;
      padding: 2px 8px;
      font-family: 'Open Sans', sans-serif;
    }
</style>





