<template lang="pug">
  .login
    img.logo(src="/hopeIT_logo.png")
    .login-form
      input.username(v-model="username", placeholder="Login")
      input.password(type="password", v-model="password", placeholder="Hasło")
      ui-button.login-button(@click="loginHandler") Zaloguj
</template>

<script>
import { mapActions } from 'vuex'
import UiButton from '../components/UiButton'

export default {
  name: 'LoginPage',
  components: {
    UiButton
  },
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    loginHandler () {
      this.login({ username: this.username, password: this.password }).then(() => this.$router.push('/'))
    },
    ...mapActions([
      'login'
    ])
  }
}
</script>

<style lang="scss" scoped>
.login {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgb(14, 68, 142);

  .logo {
    width: 200px;
  }

  .login-form {
    display: flex;
    flex-direction: column;

    input {
      font-size: 20pt;
      margin: 5px;
      border: none;
      border-radius: 5px;
      padding: 2px 8px;
      font-family: 'Open Sans', sans-serif;
      text-align: center;
    }
  }
}
</style>




