import Vue from 'vue'
import Router from 'vue-router'

import Home from '../views/Home'
import Login from '../views/Login'
import ChallengeList from '../views/ChallengeList'
import ChallengeInfo from '../views/ChallengeInfo'
import NewChallenge from '../views/NewChallenge'
import ChallengeVirtual from '../views/ChallengeVirtual'
import MessageView from '../views/MessageView'
import MessagesList from '../components/MessegesList'

import store from '../store/index.js'

Vue.use(Router)

const router = new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      component: Home,
      meta: { requiresAuth: true },
      redirect: '/challenges',
      children: [
        {
          component: ChallengeVirtual,
          path: 'challenges',
          meta: { breadcrumb: () => 'Wyzwania' },
          children: [
            {
              path: 'new',
              name: 'ChallengeNew',
              meta: { breadcrumb: () => 'Nowe wyzwanie' },
              component: NewChallenge
            },
            {
              name: 'Challenges',
              path: '',
              component: ChallengeList
            },
            {
              path: ':id',
              name: 'ChallengeInfo',
              props: true,
              meta: {
                breadcrumb: () => {
                  const challenge = store.getters['currentChallenge']
                  console.log(challenge)
                  return `Wyzwanie ${(challenge) ? challenge.title : ''}`
                }
              },
              component: ChallengeInfo
            }
          ]
        },
        {
          path: 'messages',
          component: MessageView,
          meta: { breadcrumb: () => 'Wiadomości' },
          children: [
            {
              path: ':kind/:id',
              name: 'messageList',
              props: true,
              component: MessagesList
            }
          ]
        }
      ]
    },
    {
      path: '/login',
      component: Login,
      name: 'login',
      meta: { requiresAuth: false },
      beforeEnter: (to, from, next) => {
        console.log('isLogged?', store.getters['isLoggedIn'])
        if (store.getters['isLoggedIn']) {
          next({ path: '/' })
        } else {
          next()
        }
      }
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    const logged = store.getters['isLoggedIn']
    if (!logged) {
      next({
        path: '/login'
      })
    } else {
      next()
    }
  } else {
    next()
  }
})

export default router



