<template lang="pug">
  .dotationChart
    canvas(ref="chartCanvas")
</template>

<script>
import Chart from 'chart.js'

export default {
  name: 'DotationChart',
  props: ['dataSet', 'labels', 'mainlabel'],
  data () {
    return {
      chart: undefined
    }
  },
  mounted () {
    Chart.defaults.global.defaultFontColor = 'white'
    Chart.defaults.global.defaultFontFamily = 'Open Sans'

    this.chart = new Chart(this.$refs.chartCanvas, {
      type: 'line',
      data: {
        labels: this.labels,
        datasets: [
          {
            label: this.mainlabel,
            backgroundColor: 'rgba(255, 255, 255, 0.8)',
            pointRadius: 0,
            data: this.dataSet
          }
        ]
      },
      options: {

      }
    })
    console.log('thisCHART', this.chart)
  },
  methods: {
    updateChart (dataset, labels) {
      this.chart.data.datasets[0].data = dataset
      this.chart.data.labels = labels
      this.chart.update()
    }
  },
  watch: {
    dataSet: function (newVal, oldVal) {
      this.updateChart(newVal, this.labels)
    },
    labels: function (newVal, oldVal) {
      this.updateChart(this.dataSet, newVal)
    }
  }
}
</script>

<style lang="scss" scoped>

</style>





