<template lang="pug">
  .ui-list(:style="{'flex-direction': (!invert) ? 'row' : 'row-reverse'}")
    .no-items(v-if="elements.length === 0 && !newSlot") {{placeholder}}
    ul.list(v-if="!(elements.length === 0 && !newSlot)")
      li.disabled(v-if="newSlot")
        slot(name="newSlot")
      li(v-for="element in elements", @click="handleClick(element)") 
        slot(:scope="element")
    .buttons
      .button(v-for="action in actions", @click="handleAction(action)", :class="{'active': hoverAction === action}")
        .icon.material-icons {{action.icon}}
        .desc {{action.label}}
</template>

<script>
export default {
  name: 'List',
  props: {
    elements: { type: Array },
    actions: { type: Array },
    newSlot: { type: Boolean },
    hoverAction: { type: Object },
    invert: { type: Boolean },
    placeholder: { type: String, default: 'Brak elementów' }
  },
  methods: {
    handleClick (el) {
      this.$emit('click', el)
    },
    handleAction (action) {
      this.$emit(action.name)
    }
  }
}
</script>

<style lang="scss" scoped>
.ui-list {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  
  .buttons {
    border-radius: 5px;
    padding: 2px;
    margin: 0 4px;
    background-color: rgba(0,0,0, 0.1);
    .button {
      cursor: pointer;
      width: 64px;
      height: 64px;
      color: white;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border-radius: 5px;
      transition: all 0.2s ease;

      .desc {
        font-size: 0.7em;
        text-align: center;
      }

      &:hover, &.active{
        background-color: rgba(0,0,0,0.2)
      }
    }
  }
}
.list {
    width: 500px;
    list-style-type: none;
    box-sizing: border-box;
    background-color: white;
    margin: 0 5px 0 0;
    border-radius: 5px;
    padding: 0;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);

    // max-height: 450px;
    // overflow-y: scroll;

    li:not(.no-items) {
      display: flex;
      flex-direction: row;
      align-items: center;
      height: 64px;
      position: relative;
      border-radius: 5px;
      background-color: white;
      transition: all 0.2s ease;
      width: 100%;
      left: 0px;

      &:not(.disabled) {
        cursor: pointer;
      }

      &:not(.disabled):hover {
        box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
        width: calc(100% + 8px);
        left: -4px;
        z-index: 1;
      }

      &:not(:last-child):not(:hover)::after {
        content: "";
        position: absolute;
        width: 90%;
        height: 1px;
        background-color: rgba(0,0,0, 0.1);
        bottom: 0;
        left: 50%;
        transform: translateX(-50%)
      }
    }
  }
</style>




