<template lang="pug">
  .stat
    .value 
      | {{calcValue}}
      span(v-if="money") &nbsp;PLN
    .description {{desc}}
</template>

<script>
export default {
  name: 'Stat',
  props: ['money', 'desc', 'value'],
  computed: {
    calcValue () {
      return this.value
    }
  }
}
</script>

<style lang="scss" scoped>
.stat {
  color: white;
  margin: 0 16px;
  width: 300px;

  .value {
    font-size: 48pt;
    font-weight: 800;
    line-height: 1;

    span {
      font-weight: 200;
      font-size: 24pt;
    }
  }

  .description {
    font-weight: 200;
  }
}
</style>





