const R = require('ramda')
import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'
import VueBreadcrumbs from 'vue-breadcrumbs'

import api from '../api/api.js'

Vue.use(Vuex)
Vue.use(VueBreadcrumbs)

const SET_AUTH_KEY = 'SET_AUTH_KEY'

const SET_CHALLENGES = 'SET_CHALLENGES'
const SET_CHALLENGE_ID = 'SET_CHALLENGE_ID'

const SET_MESSAGES = 'SET_MESSAGES'
const SET_USERS = 'SET_USERS'

const state = {
  authKey: null,
  challenges: [],
  currentChallengeId: null,
  messages: [],
  users: []
}

const mutations = {
  [SET_AUTH_KEY] (state, key) {
    state.authKey = key
  },
  [SET_CHALLENGES] (state, challenges) {
    state.challenges = challenges
  },
  [SET_CHALLENGE_ID] (state, id) {
    state.currentChallengeId = id
  },
  [SET_MESSAGES] (state, msgs) {
    state.messages = msgs
  },
  [SET_USERS] (state, users) {
    state.users = users
  }
}

const actions = {
  login ({ commit }, { username, password }) {
    return api.login(username, password).then((response) => {
      console.log('Logging with response: ', response)
      commit(SET_AUTH_KEY, response.data.token)
    }).catch((error) => {
      console.error(error)
    })
  },
  logout ({ commit }) {
    commit(SET_AUTH_KEY, null)
  },

  // challenges
  loadChallenges ({ commit }) {
    api.getAllChallenges().then(response => {
      commit(SET_CHALLENGES, response.data)
    })
  },

  loadChallenge ({ commit, state }, { id }) {
    if (!state.challenges.map(c => c._id).includes(id)) {
      return api.getChallenge(id).then(res => {
        const challenge = res.data[0]
        commit(SET_CHALLENGES, [...state.challenges, challenge])
        commit(SET_CHALLENGE_ID, id)
      })
    } else {
      commit(SET_CHALLENGE_ID, id)
      return Promise.resolve()
    }
  },

  loadMessages ({ commit }) {
    api.getAllMessages().then(response => {
      commit(SET_MESSAGES, response.data)
    })
  },

  loadUsers ({ commit }) {
    api.getAllUsers().then(response => {
      console.log(response)
      commit(SET_USERS, response.data)
    })
  }
}

const getters = {
  isLoggedIn: (state) => !!state.authKey,
  authKey: (state) => state.authKey,
  currentChallenge: (state) => state.challenges.find(c => c._id === state.currentChallengeId),
  messageUsers: (state) => {
    const messages = state.messages.filter(msg => msg.user_id).map(msg => msg.user_id)
    // const messagesGroupedByUsers = R.groupBy(msg => msg.user_id._id)(messages)
    return R.uniqWith((x, y) => x._id === y._id, messages)
  },
  messageChallenges: (state) => {
    const messages = state.messages.filter(msg => msg.challenge_id).map(msg => msg.challenge_id)
    return R.uniqWith((x, y) => x._id === y._id, messages)
  }
}

const store = new Vuex.Store({
  state,
  mutations,
  actions,
  getters,
  plugins: [createPersistedState({
    paths: ['authKey']
  })]
})

export default store



