<template lang="pug">
  .wrapper
    .messageList
      header {{headerText}}
      .list
        ul
          li(v-for="msg in messagesFiltered")
            .msgBox()
              img.image(v-if="msg.image_url", :src="getImageUrl(msg.image_url)")
              .content {{msg.text}}
            .time {{msg.dateMoment.fromNow()}}
      .currentMessage
        .imageSlot(v-if="uploadedImage")
        .inputBar
          ui-input.textInput(v-model="textInput")
          ui-button.addImage()
            span {{ uploadedImage === '' ? 'Dodaj obraz' : 'Dodano'}}
            file-base64.base(v-if="uploadedImage === ''", v-bind:done="imgAdded")
          ui-button.sendButton(@click="sendMsg") Wyslij
</template>

<script>
const R = require('ramda')
import moment from 'moment'
import { mapState, mapActions } from 'vuex'
import { API_BASE } from '../api/config.js'
import fileBase64 from 'vue-file-base64'
import api from '../api/api.js'

import UiButton from '../components/UiButton'
import UiInput from '../components/UiTextInput'

export default {
  node: 'MessegesList',
  components: {
    UiButton,
    UiInput,
    fileBase64
  },
  data () {
    return {
      uploadedImage: '',
      textInput: ''
    }
  },
  computed: {
    ...mapState(['messages', 'users', 'challenges']),
    messagesFiltered () {
      const userMsgs = this.messages.filter(msg => msg.user_id && msg.user_id._id === this.id)
      const challengeMsgs = this.messages.filter(msg => msg.challenge_id && msg.challenge_id._id === this.id)
      const kindFiltered = (this.kind === 'users') ? userMsgs : challengeMsgs
      // kindFiltered.forEach(msg => console.log(typeof msg.date))
      const dateSorted = R.sort((m1, m2) => moment(m1).isBefore(moment(m2) ? 1 : -1))(kindFiltered).map(msg => ({ ...msg, dateMoment: moment(msg.date) }))
      return R.reverse(dateSorted)
    },
    headerText () {
      const user = this.users.filter(u => u._id === this.id)[0]
      const challenge = this.challenges.filter(c => c._id === this.id)[0]
      if (user || challenge) {
        return (this.kind === 'users') ? ((user.name) ? user.name : '') : ((challenge.title) ? challenge.title : '')
      } else {
        return ''
      }
    }
  },
  methods: {
    ...mapActions(['loadMessages']),
    sendMsg () {
      console.log('aaa')
      api.sendMessage({
        user_id: (this.kind === 'users') ? this.id : undefined,
        challenge_id: (this.kind === 'challenges') ? this.id : undefined,
        text: this.textInput,
        image: this.uploadedImage,
        topic: 'W'
      }).then(c => {
        this.loadMessages()
        this.textInput = ''
        this.uploadedImage = ''
      }).catch(e => console.error(e))
    },
    imgAdded (files) {
      this.uploadedImage = files.base64
    },
    getImageUrl (url) {
      return API_BASE + url
    }
  },
  props: ['kind', 'id']
}
</script>

<style lang="scss" scoped>
$w: 800px;
.list {
  height: 650px;
  overflow-y: scroll;
  width: $w;
  position: relative;
  background-color: rgba(0,0,0,0.2);
  border-radius: 5px;

  .time {
    color: white;
    font-size: 10pt;
    opacity: 0.5;
    top: -12px;
    right: 40px;
    position: relative;
  }

  ul {
    list-style-type: none;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    li {
      width: 80%;
      text-align: right;
    }

    .msgBox {
      margin: 16px 0;
      padding: 8px 16px;
      // width: 100%;
      font-size: 16pt;
      border-radius: 5px;
      background-color: white;
      display: inline-block;

      &::after {
        content: "";
        background-color: white;
        width: 15px;
        height: 15px;
        position: absolute;
        top: 10;
        left: 690px;
        transform: rotate(-45deg)
      }
    }
  }
}

.base {
  height: 48px;
  position: absolute;
  top: 0px;
  left: 0;
  width: 170px;
  background-color: blue;
  opacity: 0;
}

.image {
  max-width: 300px;
}

.addImage {
  position: relative;
  overflow: hidden;
}

header {
  color: white;
  font-size: 24pt;
  font-weight: 200;
}
.inputBar {
  display: flex;
  flex-direction: row;
  width: $w;
}
.textInput {
  $tiw: $w - 280px;
  width: $tiw;
  top: 5px;
}


.wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
</style>





