<template lang="pug">
  .challengeInfo
    .columns
      .column.first-column 
        .stats
          stat(:value="sumOfMoney", :desc="'Pieniądze zgromadzone dotychczas'", :money="true")
          stat(:value="countOfMoney", :desc="'Ilość datków dotychczas'", :money="false")
        dotation-chart(
          mainlabel="Ostatnie wpłaty",
          :dataSet="lastPayments",
          :labels="lastBackers"
        )
      .column.second-column
        h2 Wpłaty:
        ui-list(:elements="payments", :actions="actions", @messageAll="$router.push('/messages/challenges/'+id)", @click="handleClick")
          .payment(slot-scope="props")
            .amount 
              .content 
                | {{Math.floor(props.scope.amount)}}
                span PLN
              .bg.material-icons attach_money
            .user {{props.scope.user.name}}
            .action.material-icons message
</template>

<script>
const padArray = (arr, len, fill) => {
  return arr.concat(Array(len).fill(fill)).slice(0, len)
}

import { mapActions, mapGetters } from 'vuex'
import moment from 'moment'
const R = require('ramda')

import Stat from '../components/Stat'

import UiList from '../components/UiList'
import DotationChart from '../components/DotationChart'

export default {
  components: {
    UiList,
    DotationChart,
    Stat
  },
  mounted () {
    this.loadChallenge({ id: this.id })

    document.querySelector('body').addEventListener('keydown', () => {
      console.log('would be updating')
      this.loadChallenge({ id: this.id })
    })
  },
  name: 'ChallengeInfo',
  computed: {
    ...mapGetters(['currentChallenge']),
    payments () {
      if (!this.currentChallenge) return []
      console.log('payments', this.currentChallenge.payments)
      return this.currentChallenge.payments.length > 0 ? this.currentChallenge.payments.map(obj => ({ amount: obj.amount, user: { name: obj.user_id.name, id: obj.user_id._id }})) : []
    },
    actions () {
      return [{ name: 'messageAll', label: 'Napisz wszystkim', icon: 'people' }]
    },
    lastPayments () {
      const paymentsOrdered = R.sort((m1, m2) => moment(m1.date).isBefore(moment(m2.date) ? -1 : 1))(this.payments).map(p => p.amount)
      if (paymentsOrdered.length < 5) {
        const padded = padArray(paymentsOrdered, 5, 0)
        return padded.reverse()
      } else {
        const fiveLast = paymentsOrdered.slice(paymentsOrdered.length - 5)
        return fiveLast.reverse()
      }
    },
    lastBackers () {
      const paymentsOrdered = R.sort((m1, m2) => moment(m1.date).isBefore(moment(m2.date) ? -1 : 1))(this.payments).map(p => {
        return p.user.name
      })
      if (paymentsOrdered.length < 5) {
        const padded = padArray(paymentsOrdered, 5, '')
        return padded.reverse()
      } else {
        const fiveLast = paymentsOrdered.slice(paymentsOrdered.length - 5)
        return fiveLast.reverse()
      }
    },
    sumOfMoney () {
      return this.payments.map(p => p.amount).reduce((prev, curr) => prev + curr, 0)
    },
    countOfMoney () {
      return this.payments.length
    }
  },
  props: ['id'],
  methods: {
    ...mapActions([
      'loadChallenge',
      'burnBreadcrumbs'
    ]),
    handleClick (el) {
      this.$router.push('/messages/users/' + el.user.id)
    }
  }
}
</script>

<style lang="scss" scoped>
  .columns {
    display: flex;
    flex-direction: row;
    justify-content: center;
    color: white;

    .column {
      flex-grow: 1;
      max-width: 700px;
    }

    .first-column {
      display: flex;
      flex-direction: column;
    }

    .stats {
      display: flex;
      flex-direction: row;
    }

    .payment {
      width: 100%;
      display: flex;
      flex-direction: row;
      align-items: center;
      color: black;

      .amount {
        font-size: 24pt;
        font-weight: 400;
        margin: 0 16px;
        position: relative;

        .content {
          position: relative;
          z-index: 2;

          span {
            font-size: 0.5em;
            opacity: 0.8;
          }
        }

        .bg {
          position: absolute;
          z-index: 1;
          font-size: 45pt;
          font-weight: 800;
          top: -8px;
          left: -20px;
          transform: rotateZ(20deg);
          color: rgb(0, 200, 0);
          opacity: 0.5;
        }
      }

      .user {
        flex-grow: 1;
      }

      .action {
        transition: all 0.2s ease;
        font-size: 24pt;
        margin: 0 16px;
        opacity: 0;
      }
      &:hover {
        .action {
          opacity: 0.5;
        }
      }
    }
    h2 {
      font-weight: 200;
    }
  }
</style>




