<template lang="pug">
  .breadcrumbs
    span.breadcrumb-item(v-for="(route, i) in currentBreadCrumbs")
      span.slash(v-if="i !== 0") &nbsp;/&nbsp;
      router-link(:to="route.path") {{route.name}}
</template>

<script>

export default {
  name: 'Breadcrumbs',
  computed: {
    currentBreadCrumbs () {
      console.log('bc', this.$breadcrumbs)
      const cBc = this.$breadcrumbs.map(route => {
        const name = (route.meta && route.meta.breadcrumb) ? route.meta.breadcrumb() : 'no-name'
        return { name, path: route.path }
      })
      console.log(cBc)
      return cBc
    }
  }
}
</script>

<style lang="scss" scoped>
  .breadcrumbs {
    flex-grow: 1;
  }

  a {
    color: white;
    text-decoration: none;
  }

  .slash {
    opacity: 0.5;
  }
</style>




